import React from 'react';
import { IRootState } from 'app/shared/reducers';
import { removeParameter, setParameter } from 'app/shared/reducers/parameters';
import { connect } from 'react-redux';
import { SDDI_APP_URL } from 'app/cls/config/constants';
import { LoadingComponent } from 'frontend-common-library';
import {generateAuthenticationRequest} from "app/shared/redirect.Component/sddi-redirect.reducer";
import {reset} from "app/cls/config/common.reducer";

export interface ISddiRedirectPageProps extends StateProps, DispatchProps {
  rtaUnifiedNo: String;
  url: String;
  hideLoadingBar?: boolean;
  openNewTab?: boolean;
  updateState?: Function;
}

export interface ISddiRedirectPageState {
  showLoadingBar: boolean;
}

export class SddiRedirectPage extends React.Component<ISddiRedirectPageProps, ISddiRedirectPageState> {
  state: ISddiRedirectPageState = {
    showLoadingBar: false,
  };

  componentDidMount = async () => {
    const sddiUrl = SDDI_APP_URL + this.props.url + '&trafficNo=' + this.props.rtaUnifiedNo;

    const request = {
      rtaUnifiedNo: this.props.rtaUnifiedNo,
      journeyDueDays: 0,
      systemCode: 'TRF',
      journeyTypeCode: 'OPEN_SDDI_DASH',
      url: sddiUrl,
    };

    await this.props.generateAuthenticationRequest(request);
    const authenticationRequest = this.props.authenticationRequest;

    if (this.props.openNewTab) {
      window.open(
        SDDI_APP_URL +
          '/index.jsp' +
          '?sourceSystem=8&dxSSOToken=' +
          authenticationRequest.authorizationCode +
          '&trafficNo=' +
          this.props.rtaUnifiedNo,
        '_blank'
      );
    } else {
      location.href =
        SDDI_APP_URL +
        '/index.jsp' +
        '?sourceSystem=8&dxSSOToken=' +
        authenticationRequest.authorizationCode +
        '&trafficNo=' +
        this.props.rtaUnifiedNo;
    }
    await this.props.updateState();
  };
  render() {
    return this.props.hideLoadingBar ? <></> : <LoadingComponent show={true} />;
  }
}

const mapStateToProps = (storeState: IRootState) => ({
  parameters: storeState.parameters.parameters,
  lang: storeState.locale.currentLocale,
  authenticationRequest: storeState.sddiRedirectState.authenticationRequest,
});

const mapDispatchToProps = {
  reset,
  setParameter,
  removeParameter,
  generateAuthenticationRequest: generateAuthenticationRequest,
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(SddiRedirectPage);
