import { FAILURE, SUCCESS } from 'app/shared/reducers/action-type.util';
import { postVerifiedRequest } from 'app/cls/config/network-service-reducer';
import { API_GENERATE_AUTHENTICATION_REQUEST } from 'app/cls/config/constants';

export const ACTION_TYPES = {
  SDDI_REDIRECT: 'SDDI-REDIRECT/GET_AuthenticationRequest',
  RESET: 'auth/RESET',
};
const initialState = {
  authenticationRequest: null,
  errorMessage: null,
};

export type ISddiRedirectState = Readonly<typeof initialState>;

// Reducer
export default (state: ISddiRedirectState = initialState, action): ISddiRedirectState => {
  switch (action.type) {
    case FAILURE(ACTION_TYPES.SDDI_REDIRECT):
      return {
        ...initialState,
        errorMessage: action.payload,
      };
    case SUCCESS(ACTION_TYPES.SDDI_REDIRECT):
      return {
        ...state,
        authenticationRequest: action.payload.data,
      };
    case ACTION_TYPES.RESET:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};

// Actions
export const generateAuthenticationRequest = request => ({
  type: ACTION_TYPES.SDDI_REDIRECT,
  payload: postVerifiedRequest(API_GENERATE_AUTHENTICATION_REQUEST, request),
});

export const reset = () => ({
  type: ACTION_TYPES.RESET,
});
