import moment from 'moment';
import { Storage } from 'react-jhipster';
import jwt_decode from 'jwt-decode';

const locale = Storage.session.get('locale', 'en');

export const isNumber = (num: string) => {
  if (num !== 'undefined' && num !== null && num !== '' && /^\d+$/.test(num)) {
    return true;
  } else {
    return false;
  }
};

export const skipAllWhiteSpace = (str: string) => {
  if (str !== 'undefined' && str !== null && str !== '') {
    return str.replace(/\s/g, '');
  } else {
    return '';
  }
};

export const skipAllSpaceWithSplit = (str: string, sub: number) => {
  if (str !== 'undefined' && str !== null && str !== '') {
    return str.replace(/\s/g, '').substring(sub);
  } else {
    return '';
  }
};

export const isDateNull = (date: Date) => {
  if (typeof date === 'undefined' || date === null) {
    return true;
  } else {
    return false;
  }
};

export const formatDate = (date: Date) => (date ? moment(date).format('YYYY-MM-DD') : '');

export const isValidDate = (date: Date) => (!isDateNull(date) ? true : false);

export const isEnglishLocale = () => locale === 'en';

export const isArabicLocale = () => locale === 'ar';

export const getObject = (obj: object) => (obj !== undefined ? obj : {});

export const getArray = (obj: any) => (obj ? obj : []);

export const getElemntValue = (obj: any) => (obj ? obj : '');

export const getNumber = (obj: object) => {
  if (obj) {
    return Number.parseInt(obj.toString(), 10);
  } else {
    return null;
  }
};

export const getDate = (date: Date) => {
  if (!isDateNull(date)) {
    return date;
  } else {
    return null;
  }
};

export const getBoolean = (boolean: boolean) => (boolean ? boolean : false);

export const isValidLicenseNumber = (licenseNumber: string) => (licenseNumber.length > 0 && licenseNumber.length <= 5 ? true : false);

export const IsJsonString = str => {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
};

export const CalculatingDiff = (date, Lday?, Ltime?) => {
  let diff = date.getTime() - new Date().getTime();

  let conDay = '';
  let conTime = '';
  let conmin = '';

  conDay = Lday && Lday !== undefined ? Lday : 'Days';
  conTime = Ltime && Ltime !== undefined ? Ltime : 'hrs';
  conmin = conmin && conmin !== undefined ? conmin : 'm';

  if (diff < 0) {
    return 0 + conDay + ' , ' + 0 + ':' + 0 + ' ' + 0;
  }

  diff = diff / 1000;
  // let seconds = Math.floor(diff % 60);
  diff = diff / 60;
  const minutes = Math.floor(diff % 60);
  diff = diff / 60;
  let hours = Math.floor(diff % 24);
  let days = Math.floor(diff / 24);
  if (days < 0) {
    days = 0;
    hours = 0;
  }

  // return (days + Lday ? Lday : ' Days' + ', ') + (hours + Ltime ? Ltime : ' hrs');
  return days + conDay + ' , ' + hours + ':' + minutes + ' ' + (hours < 1 ? conmin : conTime);
};

export const responsiveChecker = () => {
  return window.matchMedia('(max-width: 768px)').matches ? true : false;
};

export const isBlankOrNull = (value: string): boolean => {
  return value === undefined || value === '' || value === null;
};

export const isObjectBlankOrNull = (value: object): boolean => {
  return value === undefined || value === null || value === {};
};

export const maskMobileNumber = (value: string): string => {
  if (value !== '') {
    if (value.startsWith('971')) {
      return value.substr(0, 5) + '****' + value.substr(value.length - 3);
    }

    if (value.startsWith('+971')) {
      return value.substr(1, 5) + '****' + value.substr(value.length - 3);
    }
    return '971' + value.substr(1, 2) + '****' + value.substr(value.length - 3);
  }
};

export const removeDash = str => {
  if (str !== 'undefined' && str !== null && str !== '') {
    return str.replaceAll('-', '');
  } else {
    return '';
  }
};

export const isValidUAEMobileNumber = mobileNumber => {
  mobileNumber = mobileNumber.replaceAll('_', '');
  mobileNumber = mobileNumber.split(' ').join('');
  const uaePrefixes = ['+97150', '+97152', '+97154', '+97155', '+97156', '+97158'];
  const mobileNumberLength = mobileNumber.replace('+971', '').length;
  const prefix = mobileNumber.substr(0, 6);
  return mobileNumberLength === 9 && uaePrefixes.includes(prefix);
};

export const isValidEmailAddress = (emailAddress: string): boolean => {
  const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return emailRegex.test(String(emailAddress).toLowerCase());
};

export const addDash = str => {
  if (str !== 'undefined' && str !== null && str !== '') {
    const newStr =
      str.toString().substring(0, 3) +
      '-' +
      str.toString().substring(3, 7) +
      '-' +
      str.toString().substring(7, 14) +
      '-' +
      str.toString().substring(14, 15);
    return newStr;
  } else {
    return '';
  }
};

export const getTokenData = (token): any => {
  return jwt_decode(token);
};

export const getUserType = (token): 'individual' | 'corporate' | string => {
  const tokenDataClaims: any = getTokenData(token).authorities[0];
  if (tokenDataClaims === 'ROLE_VERIFIED') {
    return 'individual';
  } else if (tokenDataClaims === 'ROLE_CORPORATE_FRANCHISED' || tokenDataClaims === 'ROLE_CORPORATE_VERIFIED') {
    return 'corporate';
  }
  return tokenDataClaims;
};

export const getNotificationLevelClass = (notificationLevel: string): string => {
  switch (notificationLevel) {
    case 'WARNING':
      return 'orange';
    case 'ALERT':
      return 'red';
    case 'INFORMATION':
      return null;
    default:
      return null;
  }
};
