import { isPromise, translate } from 'react-jhipster';
import { toast } from 'react-toastify';
import { decryptResponse, setSessionStorage } from 'app/cls/config/network-service-reducer';
import { NOTIFICATION_TYPES, pushNotification } from 'app/cls/config/notificationService';

const addErrorAlert = (message, key?, data?) => {
  key = key ? key : message;
  toast.error(translate(key, data));
};
export default () => next => action => {
  // If not a promise, continue on
  if (!isPromise(action.payload)) {
    return next(action);
  }

  /**
   *
   * The notification middleware serves to dispatch the initial pending promise to
   * the promise middleware, but adds a `then` and `catch.
   */
  return next(action)
    .then(response => {
      if (action.meta && action.meta.successMessage) {
        toast.success(action.meta.successMessage);
      } else if (response && response.action && response.action.payload && response.action.payload.headers) {
        const headers = response.action.payload.headers;
        let alert: string | null = null;
        let alertParams: string | null = null;
        Object.entries<string>(headers).forEach(([k, v]) => {
          if (k.toLowerCase().endsWith('app-alert')) {
            alert = v;
          } else if (k.toLowerCase().endsWith('app-params')) {
            alertParams = decodeURIComponent(v.replace(/\+/g, ' '));
          }
        });
        if (alert) {
          const alertParam = alertParams;
          toast.success(translate(alert, { param: alertParam }));
        }
      }
      return Promise.resolve(response);
    })
    .catch(async error => {
      setSessionStorage('Error', error?.response);
      if (action.meta && action.meta.errorMessage) {
        toast.error(action.meta.errorMessage);
      } else if (error && error?.response) {
        const response = decryptResponse(error?.response);
        const data = response?.data;
        if (response.status) {
          let i;
          switch (response.status) {
            // connection refused, server not reachable
            case 503:
              setSessionStorage('serviceNotAvailable', 'true');
              location.href = '/error';
              break;

            case 504:
              setSessionStorage('serviceNotAvailable', 'true');
              location.href = '/error';
              break;

            case 404:
              // addErrorAlert('Not found', 'error.url.not.found');
              break;

            default:
              if (data.errorCategory !== '102' && data.errorCategory !== '106') {
                const systemTraceId = data.systemTraceId;
                setSessionStorage('systemTraceId', systemTraceId);
                setSessionStorage('generalError', 'true');
                location.href = '/error';
              } else if (response.status !== 200) {
                await pushNotification(response, NOTIFICATION_TYPES.error);
              }
          }
        }
      } else if (error && error.message) {
        toast.error(error.message);
      } else {
        toast.error('Unknown error!');
      }
      return Promise.reject(error);
    });
};
