import 'react-toastify/dist/ReactToastify.css';
import './app.scss';

import React, { useEffect } from 'react';
import { connect, useSelector } from 'react-redux';
import { BrowserRouter as Router } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import { hot } from 'react-hot-loader';
import { IRootState } from 'app/shared/reducers';
import { getSession } from 'app/shared/reducers/authentication';
import { getProfile } from 'app/shared/reducers/application-profile';
import { setLocale } from 'app/shared/reducers/locale';
import { hasAnyAuthority } from 'app/shared/auth/private-route';
import ErrorBoundary from 'app/shared/error/error-boundary';
import { AUTHORITIES } from 'app/config/constants';
import AppRoutes from 'app/routes';
import { RTAHeaderComponent, RTAFooterComponent } from 'frontend-common-library';
import { isRTL } from 'app/config/translation';
import jwt_decode from 'jwt-decode';

import { isBlankOrNull } from 'app/shared/util/utils';
import CallCenterBannerComponent from "app/shared/callCenterBanner/callCenterBanner";

const baseHref = document.querySelector('base').getAttribute('href').replace(/\/$/, '');

export interface IAppProps extends StateProps, DispatchProps {}

export const App = (props: IAppProps) => {
  const token: any = useSelector((state: IRootState) => {
    return state?.parameters?.parameters?.get('token');
  });

  useEffect(() => {
    if (token) {
      const tokenData = jwt_decode(token?.toString());
      const arabicName = tokenData['arabicCustomerName']?.toString()?.split(' ')[0];
      const englishName = tokenData['englishCustomerName']?.toString()?.split(' ')[0]?.toLocaleLowerCase();
      const callCenterUserName = tokenData['callCenterUserName'];

      if (arabicName && englishName) {
        const userName = {
          ar: arabicName,
          en: englishName,
        };
        setAccount(userName);
      }
      if (callCenterUserName && !isBlankOrNull(callCenterUserName)) {
        setIsCallCenterAgent(true);
      }
    }
  }, [token]);

  const [accountName, setAccount] = React.useState(null);
  const [isCallCenterAgent, setIsCallCenterAgent] = React.useState(false);

  return (
    <Router basename={baseHref}>
      <div className="app-container">
        <ToastContainer position={toast.POSITION.TOP_LEFT} className="toastify-container" toastClassName="toastify-toast" />
        <ErrorBoundary>
          <RTAHeaderComponent currentLocale={props.currentLocale} account={accountName} onLocaleChange={props.setLocale} isRTL={isRTL} />
        </ErrorBoundary>
        <div className="container umsPortal">
          {isCallCenterAgent && <CallCenterBannerComponent />}
          <ErrorBoundary>
            <AppRoutes />
          </ErrorBoundary>
        </div>

        <RTAFooterComponent happinessMeterURL={window.location.origin + '/happinessMeter'} />
      </div>
    </Router>
  );
};

const mapStateToProps = ({ authentication, applicationProfile, locale, parameters }: IRootState) => ({
  currentLocale: locale.currentLocale,
  isAuthenticated: authentication.isAuthenticated,
  isAdmin: hasAnyAuthority(authentication.account.authorities, [AUTHORITIES.ADMIN]),
  ribbonEnv: applicationProfile.ribbonEnv,
  isInProduction: applicationProfile.inProduction,
  isSwaggerEnabled: applicationProfile.isSwaggerEnabled,
  parameters: parameters.parameters,
});

const mapDispatchToProps = { setLocale, getSession, getProfile };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(hot(module)(App));
