import React from 'react';
import { translate } from 'react-jhipster';
import './callCenterBanner.scss';
import { IRootState } from 'app/shared/reducers';
import { connect } from 'react-redux';
import { setParameter } from 'app/shared/reducers/parameters';
import { getTokenData, getUserType } from 'app/shared/util/utils';
import moment from 'moment';
import { HELP_DESK_SYSTEM_URL, SDDI_APP_URL } from 'app/cls/config/constants';
import SddiRedirectPage from "app/shared/redirect.Component/sddiRedirect";
import {updateOrgLoginMode, updatePersonLoginMode} from "app/cls/config/common.reducer";


class CallCenterBannerComponent extends React.Component<any, any> {
  state: Readonly<any> = {
    goToSddi: false,
    rtaUnifiedNo: null,
    tokenData: null,
    agentEnName: null,
    agentArName: null,
    sddiURL: '',
    userType: null,
  };
  componentDidMount(): void {
    const token = this.props.parameters.get('token');
    const tokenData = getTokenData(this.props.parameters.get('token'));
    const agentEnName = tokenData.callCenterUserEnglishName;
    const agentArName = tokenData.callCenterUserArabicName;
    const rtaUnifiedNo = tokenData.rtaUnifiedNo;
    const userType = getUserType(token);
    this.setState({ agentArName, agentEnName, tokenData, rtaUnifiedNo, userType });
  }

  updateLoginMode = async () => {
    await this.setState({ showLoadingBar: true });

    const request = {
      syncEntityId: null,
      loginMode: 'SDDI',
    };

    await this.setState({
      goToSddi: true,
      sddiURL: `/main-dashboard.do?callCenterCustomerDashboard=SDDI&switchLanguage=${this.props.lang}`,
    });
  };

  goToSddiPage = path => {
    this.setState({ goToSddi: true, sddiURL: path });
  };

  render() {
    return (
      <>
        <div className="CallCenterBannerComponent">
          <div className="left-side">
            <h3>{this.props.lang === 'en' ? this.state.agentEnName : this.state.agentArName}</h3>
            <p>
              {translate('callCenterBanner.lastLogin')}: {moment().format('DD/MM/YYYY')}
            </p>
          </div>
          <div className="right-side">
            <ul className="list-unstyled">
              <li>
                <a onClick={() => this.goToSddiPage(`/general-reports.do?switchLanguage=${this.props.lang}`)}>
                  {translate('callCenterBanner.reports')}
                </a>
              </li>
              <li>
                <a onClick={() => this.goToSddiPage(`/user_manual.do?switchLanguage=${this.props.lang}`)}>
                  {translate('callCenterBanner.viewManual')}
                </a>
              </li>
              <li>
                <a href="https://sdk.emiratesid.ae/" target="_blank" rel="noreferrer">
                  {translate('callCenterBanner.downloadEIDReaderSW')}
                </a>
              </li>
              <li>
                <a href={SDDI_APP_URL + '/logout.do'}>{translate('callCenterBanner.backHomepage')}</a>
              </li>
              <li>
                <a onClick={this.updateLoginMode}>{translate('callCenterBanner.switchOldDashboard')}</a>
              </li>
              <li>
                <a
                  onClick={() =>
                    this.goToSddiPage(
                      `/cc/find-call-center-customer-profile.do?actionParam=searchForNewCustomer&switchLanguage=${this.props.lang}`
                    )
                  }
                >
                  {translate('callCenterBanner.backSearchPage')}
                </a>
              </li>
              <li>
                <a href={HELP_DESK_SYSTEM_URL} target="_blank" rel="noreferrer">
                  {translate('callCenterBanner.helpDeskSystem')}
                </a>
              </li>
            </ul>
          </div>
        </div>
        {this.state.goToSddi && <SddiRedirectPage rtaUnifiedNo={this.state.rtaUnifiedNo} url={`${this.state.sddiURL}`} />}
      </>
    );
  }
}

const mapDispatchToProps = {
  setParameter,
  updatePersonLoginMode,
  updateOrgLoginMode,
};

const mapStateToProps = (storeState: IRootState) => ({
  lang: storeState.locale.currentLocale,
  parameters: storeState.parameters.parameters,
  updateLoginMode: storeState.commonState.updateLoginMode,
});

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(CallCenterBannerComponent);
