import { toast } from 'react-toastify';
import { Storage, translate } from 'react-jhipster';

interface IToastMSgType {
  message: string;
  notificationType?: string;
}

const getMSG = (msg: any): IToastMSgType => {
  const locale = Storage.session.get('locale', 'en');
  try {
    if (msg !== null && typeof msg === 'string') {
      return { message: msg };
    } else if (msg !== null && (typeof msg === 'object' || msg instanceof Object)) {
      if (msg.data) {
        return {
          message:
            msg.data.errorCategory === '102' || msg.data.errorCategory === '106'
              ? locale === 'en'
                ? msg.data.messageEn
                : msg.data.messageAr
              : String(translate('common.errorMSG', { error: msg.data.systemTraceId })),
          notificationType: msg.data.validationType
            ? msg.data.validationType === 'ERROR'
              ? NOTIFICATION_TYPES.error
              : msg.data.validationType === 'WARNING'
              ? NOTIFICATION_TYPES.warn
              : NOTIFICATION_TYPES.error
            : null,
        };
      }
      return {
        message:
          msg.errorCategory === '102' || msg.errorCategory === '106'
            ? locale === 'en'
              ? msg.data.messageEn
              : msg.data.messageAr
            : String(translate('common.errorMSG', { error: msg.systemTraceId })),
        notificationType: msg.validationType
          ? msg.validationType === 'ERROR'
            ? NOTIFICATION_TYPES.error
            : msg.validationType === 'WARNING'
            ? NOTIFICATION_TYPES.warn
            : NOTIFICATION_TYPES.error
          : null,
      };
    }
  } catch (e) {
    console.error(e);
  }
};
const getToastOptions = () => ({
  position: toast.POSITION.TOP_LEFT,
  autoClose: 15000,
});

export const pushNotification = async (obj, type) => {
  const toastOpt: any = await getToastOptions();
  const toastMessage: IToastMSgType = await getMSG(obj);
  if (toastMessage.notificationType) {
    switch (toastMessage.notificationType) {
      case NOTIFICATION_TYPES.success:
        toast.success(toastMessage.message, toastOpt);
        break;
      case NOTIFICATION_TYPES.info:
        toast.info(toastMessage.message, toastOpt);
        break;
      case NOTIFICATION_TYPES.warn:
        toast.warn(toastMessage.message, toastOpt);
        break;
      case NOTIFICATION_TYPES.error:
        toast.error(toastMessage.message, toastOpt);
        break;
      default:
        toast.error(toastMessage.message, toastOpt);
        break;
    }
  } else {
    switch (type) {
      case NOTIFICATION_TYPES.success:
        toast.success(toastMessage.message, toastOpt);
        break;
      case NOTIFICATION_TYPES.info:
        toast.info(toastMessage.message, toastOpt);
        break;
      case NOTIFICATION_TYPES.warn:
        toast.warn(toastMessage.message, toastOpt);
        break;
      case NOTIFICATION_TYPES.error:
        toast.error(toastMessage.message, toastOpt);
        break;
      default:
        toast.error(toastMessage.message, toastOpt);
        break;
    }
  }
};

export const NOTIFICATION_TYPES = {
  success: 'success',
  info: 'info',
  warn: 'warn',
  error: 'error',
};

export const pushNotificationFromBackEnd = data => {
  if (data) {
    toast.info(data.message, {
      position: 'top-right',
      autoClose: 10000,
      hideProgressBar: true,
      closeOnClick: false,
      pauseOnHover: true,
      draggable: false,
    });
  }
};
