import React from 'react';
import { Switch } from 'react-router-dom';
import ErrorBoundaryRoute from 'app/shared/error/error-boundary-route';
import PageNotFound from 'app/shared/error/page-not-found';
import HomeComponent from './cls/module/home/home';
import LineChart from './cls/module/charts/lineChart';
import BarChart from './cls/module/charts/barChart';
import BarRaceChart from './cls/module/charts/barRaceChart';
import PieChart from './cls/module/charts/PieChart';
import ScatterChart from './cls/module/charts/scatterChart';
import HeatmapChart from './cls/module/charts/heatmapChart';
import GaugeChart from './cls/module/charts/gaugeChart';
import BoxplotChart from './cls/module/charts/boxplotChart';
import MultipleBarChart from './cls/module/charts/multipleBarChart';
import ScatterRaceChart from './cls/module/charts/scatterRaceChart';
import BarFlagRaceChart from './cls/module/charts/barFlagRaceChart';
import StackedColumnChart from './cls/module/charts/stackedColumnChart';

const Routes = () => (
  <div className="view-routes">
    <Switch>
      <ErrorBoundaryRoute exact path='/' component={HomeComponent} />
      <ErrorBoundaryRoute exact path='/Line-chart' component={LineChart} />
      <ErrorBoundaryRoute exact path='/Bar-chart' component={BarChart} />
      <ErrorBoundaryRoute exact path='/BarRace-chart' component={BarRaceChart} />
      <ErrorBoundaryRoute exact path='/Pie-chart' component={PieChart} />
      <ErrorBoundaryRoute exact path='/Scatter-chart' component={ScatterChart} />
      <ErrorBoundaryRoute exact path='/Heatmap-chart' component={HeatmapChart} />
      <ErrorBoundaryRoute exact path='/Gauge-chart' component={GaugeChart} />
      <ErrorBoundaryRoute exact path='/Boxplot-chart' component={BoxplotChart} />
      <ErrorBoundaryRoute exact path='/MultipleBar-chart' component={MultipleBarChart} />

      <ErrorBoundaryRoute exact path='/ScatterRace-chart' component={ScatterRaceChart} />
      <ErrorBoundaryRoute exact path='/BarFlagRace-chart' component={BarFlagRaceChart} />
      <ErrorBoundaryRoute exact path='/StackedColumn-chart' component={StackedColumnChart} />
      
      
      
      <ErrorBoundaryRoute component={PageNotFound} />
    </Switch>
  </div>
);

export default Routes;
