import { postVerifiedRequest } from 'app/cls/config/network-service-reducer';
import { API_UPDATE_ORG_LOGIN_MODE, API_UPDATE_PERSON_LOGIN_MODE } from 'app/cls/config/constants';
import { FAILURE, SUCCESS } from 'app/shared/reducers/action-type.util';

export const ACTION_TYPES = {
  UPDATE_PERSON_LOGIN_MODE: 'COMMON/UPDATE_PERSON_LOGIN_MODE',
  UPDATE_ORG_LOGIN_MODE: 'COMMON/UPDATE_ORG_LOGIN_MODE',
  RESET: 'COMMON/RESET',
};

const initialState = {
  errorMessage: null,
  updateLoginMode: '',
};
export type ICommonState = Readonly<typeof initialState>;

export default (state: ICommonState = initialState, action): ICommonState => {
  switch (action.type) {
    case FAILURE(ACTION_TYPES.UPDATE_PERSON_LOGIN_MODE):
      return {
        ...state,
        errorMessage: action.payload,
      };
    case SUCCESS(ACTION_TYPES.UPDATE_PERSON_LOGIN_MODE):
      return {
        ...state,
        updateLoginMode: action.payload.data,
      };
    case FAILURE(ACTION_TYPES.UPDATE_ORG_LOGIN_MODE):
      return {
        ...state,
        errorMessage: action.payload,
      };
    case SUCCESS(ACTION_TYPES.UPDATE_ORG_LOGIN_MODE):
      return {
        ...state,
        updateLoginMode: action.payload.data,
      };
    case ACTION_TYPES.RESET:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};

// Actions
export const updatePersonLoginMode = data => ({
  type: ACTION_TYPES.UPDATE_PERSON_LOGIN_MODE,
  payload: postVerifiedRequest(API_UPDATE_PERSON_LOGIN_MODE, data),
});

export const updateOrgLoginMode = data => ({
  type: ACTION_TYPES.UPDATE_ORG_LOGIN_MODE,
  payload: postVerifiedRequest(API_UPDATE_ORG_LOGIN_MODE, data),
});
export const reset = () => ({
  type: ACTION_TYPES.RESET,
});
