import axios, { AxiosRequestConfig } from 'axios';
import { Storage } from 'react-jhipster';
import CryptoJS from 'crypto-js';
import { IsJsonString } from 'app/shared/util/utils';
import { APP_PARAMETERS, CRYPTO_SECRET_KEY, IS_REQUEST_ENCRYPTION_ENABLED, TOKEN, GATEWAY_SERVER_API_URL } from 'app/cls/config/constants';

const TIMEOUT = 10 * 60 * 1000;
axios.defaults.timeout = TIMEOUT;

export async function getRequest(url, params) {
  const response = await axios.get(GATEWAY_SERVER_API_URL + url, { params });
  return decryptResponse(response);
}

export async function postRequest(url, data, token) {
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  const response = await axios.post(GATEWAY_SERVER_API_URL + url, encryptBase64DataAES(data));
  return decryptResponse(response);
}

export async function putRequest(url, data, token, ifMatch) {
  if (ifMatch !== null) {
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
    axios.defaults.headers.common['If-Match'] = ifMatch;
  } else {
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  }
  const response = await axios.put(GATEWAY_SERVER_API_URL + url, encryptBase64DataAES(data));
  return decryptResponse(response);
}

export async function getVerifiedRequest(url, config?: AxiosRequestConfig) {
  const token = getParameterByKey(TOKEN);
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  let response = await axios.get(GATEWAY_SERVER_API_URL + url, config);
  response = await decryptResponse(response);
  if (response.data.id_token) {
    return response.data.id_token;
  } else {
    return response;
  }
}

export async function postVerifiedRequest(url, data) {
  const token = getParameterByKey(TOKEN);
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  const response = await axios.post(GATEWAY_SERVER_API_URL + url, encryptBase64DataAES(data));
  return decryptResponse(response);
}

export async function deleteVerifiedRequest(url) {
  const token = getParameterByKey(TOKEN);
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  const response = await axios.delete(GATEWAY_SERVER_API_URL + url);
  return decryptResponse(response);
}

export function decryptResponse(response) {
  if (IS_REQUEST_ENCRYPTION_ENABLED === 'false') {
    return response;
  }
  if (response.data._data) {
    const decryptedData = decryptBase64DataAES(response.data._data);
    const jsonResult = getValidJson(decryptedData);
    return { ...response, data: jsonResult };
  } else {
    return response;
  }
}

export function getValidJson(data) {
  try {
    return JSON.parse(data);
  } catch (e) {
    return data;
  }
}

export function decryptBase64DataAES(data, escapeDecryption?: boolean) {
  if (IS_REQUEST_ENCRYPTION_ENABLED === 'false' || escapeDecryption) {
    return data;
  }

  const keyForCryptoJS = CryptoJS.enc.Base64.parse(CRYPTO_SECRET_KEY);
  const decodeBase64 = CryptoJS.enc.Base64.parse(data.toString());

  const decryptedData = CryptoJS.AES.decrypt({ ciphertext: decodeBase64 }, keyForCryptoJS, {
    mode: CryptoJS.mode.CBC,
    iv: keyForCryptoJS,
    blockSize: 16,
    padding: CryptoJS.pad.Pkcs7,
  });
  const decryptedText = decryptedData.toString(CryptoJS.enc.Utf8);

  return decryptedText;
}

export function encryptData(data) {
  if (IS_REQUEST_ENCRYPTION_ENABLED === 'false') {
    return data;
  }
  const rowData = JSON.stringify(data).trim();
  const keyForCryptoJS = CryptoJS.enc.Base64.parse(CRYPTO_SECRET_KEY);
  const encrypted = CryptoJS.AES.encrypt(rowData, keyForCryptoJS, {
    mode: CryptoJS.mode.CBC,
    iv: keyForCryptoJS,
    blockSize: 16,
    padding: CryptoJS.pad.Pkcs7,
  });

  return encrypted.toString();
}

export function encryptBase64DataAES(data) {
  if (IS_REQUEST_ENCRYPTION_ENABLED === 'false') {
    return data;
  }
  return { _data: encryptData(data) };
}

export function getLocalStorage(key, escapeDecryption?: boolean) {
  let data = Storage.local.get(key);
  if (data !== undefined) {
    data = decryptBase64DataAES(data, escapeDecryption);
    if (IsJsonString(data)) {
      return JSON.parse(data);
    } else {
      return data;
    }
  } else {
    return {};
  }
}

export function setLocalStorage(key, data) {
  if (data) {
    Storage.local.set(key, encryptData(data));
  } else {
    Storage.local.set(key, {});
  }
}

export function getSessionStorage(key) {
  let data = Storage.session.get(key);
  if (data !== undefined && data !== null && Object.keys(data).length > 0) {
    data = decryptBase64DataAES(data);
    if (IsJsonString(data)) {
      return JSON.parse(data);
    } else {
      return data;
    }
  } else {
    return null;
  }
}

export function setSessionStorage(key, data) {
  if (data) {
    Storage.session.set(key, encryptData(data));
  } else {
    Storage.session.set(key, {});
  }
}

export function getParameterByKey(key: string) {
  const data = new Map(getSessionStorage(APP_PARAMETERS)).get(key);
  if (data) {
    return data;
  } else {
    return undefined;
  }
}

export function decryptBase64AES(data) {
  const keyForCryptoJS = CryptoJS.enc.Base64.parse(CRYPTO_SECRET_KEY);
  const decodeBase64 = CryptoJS.enc.Base64.parse(data);

  const decryptedData = CryptoJS.AES.decrypt({ ciphertext: decodeBase64 }, keyForCryptoJS, {
    mode: CryptoJS.mode.CBC,
    iv: keyForCryptoJS,
    blockSize: 16,
    padding: CryptoJS.pad.Pkcs7,
  });
  const decryptedText = decryptedData.toString(CryptoJS.enc.Utf8);

  return decryptedText;
}
