import React from "react";
import * as echarts from "echarts";
import { Link } from "react-router-dom";
type EChartsOption = echarts.EChartsOption;

const GaugeChart = (props) => {
  let option: EChartsOption;
  let myChart;
  option = {
    series: [
      {
        type: "gauge",
        axisLine: {
          lineStyle: {
            width: 30,
            color: [
              [0.3, "#67e0e3"],
              [0.7, "#37a2da"],
              [1, "#fd666d"],
            ],
          },
        },
        pointer: {
          itemStyle: {
            color: "auto",
          },
        },
        axisTick: {
          distance: -30,
          length: 8,
          lineStyle: {
            color: "#fff",
            width: 2,
          },
        },
        splitLine: {
          distance: -30,
          length: 30,
          lineStyle: {
            color: "#fff",
            width: 4,
          },
        },
        axisLabel: {
          color: "auto",
          distance: 40,
          fontSize: 20,
        },
        detail: {
          valueAnimation: true,
          formatter: "{value} km/h",
          color: "auto",
        },
        data: [
          {
            value: 70,
          },
        ],
      },
    ],
  };

  React.useEffect(() => {
    chart();
  }, []);

  const chart = () => {
    const chartDom = document.getElementById("echartLine")!;
    myChart = echarts.init(chartDom);
    myChart.setOption(option);
  };

  setInterval(function () {
    myChart.setOption({
      series: [
        {
          data: [
            {
              value: +(Math.random() * 100).toFixed(2),
            },
          ],
        },
      ],
    });
  }, 2000);

  return (
    <>
      <Link to={"/"} className="backToHome">
        Back
      </Link>
      <div id="echartLine" style={{ width: "100%", height: "500px" }} />
    </>
  );
};

export default GaugeChart;
